<template>
  <div id="app">
    <!-- <div
      class="base-spinner spinner-border text-light"
      v-if="$store.state.loadingCount"
      role="status"
    >
      <span class="sr-only">Loading...</span>
    </div>-->
    <div class="loading-div" v-if="$store.state.loadingCount"></div>
    <div class="lds-ellipsis" v-if="$store.state.loadingCount" test>
      <div></div>
      <div></div>
      <div></div>
      <div></div>
    </div>
    <!-- <div id="nav">
      <router-link to="/home">Home</router-link>|
      <router-link to="/about">About</router-link>
    </div>-->
    <!-- <transition name="slide" mode="out-in"> -->
    <!-- <transition name> -->
    <!-- <keep-alive include="MainMenu"> -->
    <!-- <router-view :key="$route.fullPath" /> -->
    <keep-alive>
      <router-view :key="$route.name" v-if="$route.meta.keepAlive"></router-view>
    </keep-alive>
    <router-view v-if="!$route.meta.keepAlive"></router-view>
    <!-- </keep-alive> -->
    <!-- </transition> -->

    <div
      v-if="$store.state.messageText"
      class="alert bg-warning text-dark message-text elementToFadeInAndOut"
      role="alert"
      @click="clearMessageText"
    >
      <i class="fas fa-volume-up"></i>
      {{ $store.state.messageText }}
    </div>
  </div>
